.add-media-headings {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
    font-weight: 600;
}

.add-media-placeholders {
    color: #9a9a9a;
    font-family: 'Titillium Web', sans-serif;
    font-size: 15px;
    font-weight: 400;
}

.button {
    background: none;
    border: none;
    padding: 0;
}

.close-button {
    background-color: #212121;
    border: none;
    border-radius: 10px;
    height: 64px;
    position: relative;
    top: 60px;
    left: 225px;
    width: 78px;
}

.custom-modal-style {
    background-color: #101010;
    height: 100vh;
}

.custom-upload {
    align-items: center;
    background-color: #383838;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    height: 38px;
    justify-content: center;
    width: 113px;
}

input[type="file"] {
    display: none;
}

.input-fields {
    background-color: #212121;
    border: none;
    border-radius: 10px;
    color: #ffffff;
    height: 38px;
    padding: 10px;
}

.landing {
    background-color: #101010;
    background-image: url('./imgs/Background Image.svg');
    background-position: -625px -786px;
    background-repeat: no-repeat;
    height: 100vh;
    max-width: 100%;
    padding: 0;
}

.light-gray-background {
    background-color: #383838;
}

.main-container {
    align-items: center;
    display: flex;    
    height: calc(100% - 86px);
    max-width: 75.1%;
}

.main-heading {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 55px;
    font-weight: 300;
}

.main-text {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 20px;
    font-weight: 300;
}

.modal-container {
    align-items: flex-end;
    display: flex;
    height: 100vh;
    max-width: 70%;
}

.modal-xl {
    max-width: 100%;
}

.navbar-container {
    max-width: 75.1%;
    padding: 0;
}

.navbar-dark {
    background-color: #212121;
    min-height: 86px;
}

.navbar-headings {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 16px;
    font-weight: 300;
}

.numbers {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 15px;
    font-weight: 700;
}

ol { 
    counter-reset: item;
    list-style-type: none;
}

li { display: block; }

li:before { 
    content: counter(item) '  '; 
    counter-increment: item;
    position: relative;
    top: 40px;
    left: 40px;
}

.playlist-container {
    background-color: #212121;
    height: 1000vh;
    max-height: 1000vh;
    max-width: 70%;
    padding-top: 23px;
}

.playlist-song {
    background-color: #383838;
    border-radius: 5px;
    height: 63px;
    margin-bottom: 14px;
}

.semibold {
    font-weight: 600;
}

.song-name {
    color: #ffffff;
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
    font-weight: 400;
}

.thumbnail {
    max-height: 42px;
    max-width: 75px;
}

.time-added {
    color: #9a9a9a;
    font-family: 'Titillium Web', sans-serif;
    font-size: 15px;
    font-weight: 400;
}

.time-info {
    padding-right: 24px;
}

::-webkit-scrollbar {
    width: 9px;
}

::-webkit-scrollbar-track {
    background-color: #848484;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background-color: transparent;
    background: linear-gradient(180deg, #6cffe0 0%, #517aee 100%);
    border-radius: 99px;
}